#head {
  position: relative;
  background: url("../../assets/bgImg.png");
  background-size: cover;
  z-index: 0;
  background-position: center;
}

.header {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  width: 100%;
  height: 100vh;
  padding: 6rem 2rem 0;
  overflow: hidden;
  @media screen and (max-width: 660px) {
    flex-direction: column;
  }
  &__info {
    width: 25%;
    @media screen and (max-width: 660px) {
      z-index: 2;
      width: 250px;
    }

    &-container1 {
      display: flex;
      background-color: rgba(255, 255, 255, 0.5);
      box-shadow: 0 0 10px rgb(168, 168, 168);
      border-radius: 10px;
      padding: 5px;
      margin-bottom: 10px;

      span {
        font-size: 2.5rem;
        object-fit: contain;
      }
    }
    &-container2 {
      box-shadow: 0 0 10px rgb(168, 168, 168);
      margin-top: 30px;
      padding: 5px;
      border-radius: 10px;
      width: 80%;
      background-color: rgba(255, 255, 255, 0.5);
    }
  }
  &__image {
    width: 75%;
    display: flex;
    margin: 10px;
    align-items: center;
    @media screen and (max-width: 660px) {
      flex-direction: column;
      width: 100%;
    }
    &-profile {
      position: relative;
      width: 70%;
      padding: 2rem;

      &-front {
        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: contain;
          z-index: 1;
        }
      }

      &-background {
        img {
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: contain;
          z-index: -1;
        }
      }
    }

    &-essential {
      width: 30%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      &-java {
        background-color: yellow;
        box-shadow: 0 0 10px rgb(168, 168, 168);
      }
      &-bootstrap {
        background-color: white;
        box-shadow: 0 0 10px rgb(168, 168, 168);
      }
      &-react {
        background-color: rgb(41, 41, 41);
        box-shadow: 0 0 10px rgb(168, 168, 168);
      }
      &-sass {
        background-color: rgb(215, 215, 215);
        box-shadow: 0 0 10px rgb(168, 168, 168);
      }

      @media screen and (max-width: 660px) {
        flex-direction: row;

        width: 100%;
      }

      img {
        max-width: 100%;
      }
    }
  }
}
[class^="header__image-essential-"] {
  width: 70px;
  height: 70px;
  margin: 10px;
  padding: 10px;

  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  z-index: 3;
}
